/* eslint-disabe no-unused-expressions */
import ContentService from '../assets/js/amplience-content-service';
import contentTypeMap from './content-types';

export const getQueryVar = (url, queryVariable) => {
  const query = url.substring(1);
  const variables = query.split('&');
  let queryVariableValue = null;

  variables.forEach((variable) => {
    const pair = variable.split('=');
    if (decodeURIComponent(pair[0]) === queryVariable) {
      queryVariableValue = decodeURIComponent(pair[1]);
    }
  });

  return queryVariableValue;
};

export const getContentType = async (url) => {
  const content = new ContentService(`https://${getQueryVar(url, 'src')}`);
  const res = await content.getContent();
  let contentType = null;

  res['@type'] ? contentType = res['@type'] : contentType = { error: true };

  return contentType;
};

export const getContentComponent = async (url) => {
  const contentType = await getContentType(url);
  const contentTypeId = contentType.split('/')[3];

  return contentTypeMap[contentTypeId];
};

export const createComponentFilename = component => `${component.filename}.build.js`;

export const createComponentElement = (component) => {
  const componentElement = document.createElement('div');
  componentElement.id = component.el;
  componentElement.setAttribute('data-content', `https:${getQueryVar(window.location.search, 'src')}`);

  return componentElement;
};


export const createComponentAsset = (component) => {
  const componentScript = document.createElement('script');
  componentScript.src = `https://cdn.halfords-content.com/content/stage/${createComponentFilename(component)}`;

  return componentScript;
};


export const createComponentVisualisation = async (url) => {
  const component = await getContentComponent(url);

  const componentElement = createComponentElement(component);
  const componentAssetJS = createComponentAsset(component);

  document.getElementById('visualisation').appendChild(componentElement);
  document.getElementsByTagName('head')[0].appendChild(componentAssetJS);
};

createComponentVisualisation(window.location.search);



